body {
  margin: 0;
  padding: 0;
}

.dev-wrapper {
  // width: 1900px;
  margin: 0 auto;
  width: 100%;
  position: relative;
}
.dev-viewport {
  width: 100%;
  height: 100%;
  position: relative;
}
.dev-slider {
  padding: 0;
  margin: 0;
  position: relative;
  list-style-type: none;

  &:after {
    content: '.-.';
    display: block;
    clear: both;
    visibility: hidden;
    line-height: 0;
    height: 0;
  }
}
