/*------------------------------------*\
    # Direction Nav
\*------------------------------------*/

.dev-direction-nav-block {

}
.dev-direction-item {
    display: block;
    margin: -16px 0 0;
    position: absolute;
    top: 50%;
    z-index: 10;
    cursor: pointer;
    text-indent: -9999px;
    width: 24px;
    height: 32px;
}

.dev-direction-prev {
    // Dark
    // background: url(data:image/svg+xml;utf8;base64,) no-repeat;
    // White
    background: url(data:image/svg+xml;utf8;base64,) no-repeat;
    background-size: cover;
    background-position: center center;
    left: 15px;
}

.dev-direction-next {
    // Dark
    // background: url(data:image/svg+xml;utf8;base64,) no-repeat;
    // White
    background: url(data:image/svg+xml;utf8;base64,) no-repeat;
    right: 15px;
    background-size: cover;
    background-position: center center;
}



/*------------------------------------*\
    # Pagination Nav
\*------------------------------------*/

.dev-pagination-nav-block {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 20px;
    display: block;
    z-index: 100;
    text-align: center;
}

.dev-bullet {
    border-radius: 50%;
    background-color: transparent;
    transition: opacity 0.3s ease;
    width: 14px;
    height: 14px;
    display: inline-block;
    vertical-align: middle;
    border: 2px solid #fff;
    position: relative;
    cursor: pointer;
    text-indent: -9999px;

    &:after {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        border-radius: 50%;
        background-color: rgb(255, 255, 255);
        transform: scale(0);
        transform-origin: 50% 50%;
        transition: transform 0.3s ease;
    }

    + .dev-bullet {
        margin-left: 5px;
    }

    &:hover,
    &.dev-active {
        background: #fff;
        &:after {
            transform: scale(1.2);
        }
    }
}




/*------------------------------------*\
    # Auto Controls
\*------------------------------------*/

.dev-auto-control-block {
  position: absolute;
  right: 15px;
  bottom: 15px;
}
.dev-auto-control-item {
  width: 10px;
  height: 10px;
  display: inline-block;
  opacity: 1;
  transition: opacity 250ms;
  opacity: 0.5;
  cursor: pointer;
  margin-left: 8px;
  margin-right: 8px;
  vertical-align: middle;
}
.dev-auto-control-start {
    // dark
    // background: url(data:image/svg+xml;utf8;base64,) no-repeat;
    //
    // white
    background: url(data:image/svg+xml;utf8;base64,);
    width: 14px;
    height: 14px;
    background-size: cover;
}
.dev-auto-control-stop {
    // dark
    // background: url(data:image/svg+xml;utf8;base64,) no-repeat;
    //
    // white

    background: url(data:image/svg+xml;utf8;base64,);
    width: 12px;
    height: 12px;
    background-size: cover;
}
.dev-auto-control-start-active {
    opacity: 1;
}
.dev-auto-control-stop-active {
    opacity: 1;
}



/*------------------------------------*\
    # Ken Burn
\*------------------------------------*/


// Bar
// ------------

.dev-ken-burn-wrapper {
  width: 100%;
  height: 5px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
}
.dev-ken-burn-progress {
  width: auto;
  height: 100%;
  // background: rgba(0,0,0, 0.15);
  background: rgba(255,255,255, 0.35);
}


// Timer
// ------------

.dev-bar-timer {
  position: absolute;
  width: 0;
  height: 2px;
  background: white;
  border-bottom: 2px solid #555;
  opacity: .55;
  z-index: 4;
  top: 0;
}
.dev-circle-timer {
  width: 16px;
  height: 16px;
  position: absolute;
  right: 10px;
  top: 10px;
  z-index: 4;
  opacity: .65;
}
.dev-ct-half {
  background: white;
}
.dev-ct-center {
  background: #444;
}
.dev-ct-left,
.dev-ct-right {
  width: 50%;
  height: 100%;
  overflow: hidden;
}
.dev-ct-left,
.dev-ct-right {
  float: left;
  position: relative;
}
.dev-ct-rotate {
  width: 200%;
  height: 100%;
  position: absolute;
  top: 0;
}
.dev-ct-left .dev-ct-rotate,
.dev-ct-right .dev-ct-hider,
.dev-ct-right .dev-ct-half {
  left: 0;
}
.dev-ct-right .dev-ct-rotate,
.dev-ct-left .dev-ct-hider,
.dev-ct-left .dev-ct-half {
  right: 0;
}
.dev-ct-hider,
.dev-ct-half {
  position: absolute;
  top: 0;
}
.dev-ct-hider {
  width: 50%;
  height: 100%;
  overflow: hidden;
}
.dev-ct-half {
  width: 200%;
  height: 100%;
}
.dev-ct-center {
  width: 50%;
  height: 50%;
  left: 25%;
  top: 25%;
  position: absolute;
}
.dev-ct-half,
.dev-ct-center {
  border-radius: 100px;
}



/*------------------------------------*\
    # Preloader
\*------------------------------------*/

.dev-preloader {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -11px;
  margin-left: -11px;
  background: url(../../assets/images/spinner.gif) no-repeat;
  background-size: cover;
  width: 32px;
  height: 32px;
  z-index: 100;
}



